export default {
  data() {
    return {};
  },

  computed: {
    city: {
      get() {
        return this.$store.getters.city;
      },
      set(value) {
        this.$store.commit("updateCity", value);
        // this.$store.commit("updateHistory", value);
      },
    },
  },

  methods: {
    fetchData() {
      this.$store.dispatch("fetchWeather");
    },
  },
};
